@import "./App.scss";

.work-container {
  display: flex;
  margin-bottom: 3rem;
  width: 90%;
  .work-description {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin: 1rem;

    .description {
      font-family: "Roboto", sans-serif;
      margin-bottom: 1rem;
      width: 80%;
      color: gray;
      line-height: 150%;
      span {
        font-weight: 700;
        text-decoration: underline;
      }
    }
    button {
      width: 150px;
      height: 50px;
      cursor: pointer;
      background: white;
      transition: all 0.3s ease;
      border: 1px solid black;
      margin-right: 1rem;
      &:hover {
        background: #f9ea9a;
        transition: all 0.3s ease;
      }
    }
  }
  .work-name {
    font-family: "Roboto", sans-serif;
    letter-spacing: 3px;
    font-size: 32px;
    margin-bottom: 1rem;
    a {
      text-decoration: none;
      color: rgb(0, 141, 216);
      &:hover {
        text-decoration: underline;
        transition: all 0.3s ease;
      }
    }
  }
  .work-thumbnail {
    position: relative;

    .skills-used {
      display: flex;
      justify-content: center;
      align-items: center;
      .fab {
        margin: 1rem;
        font-size: 24px;
      }
    }
    img {
      width: 300px;
      height: 200px;
      box-shadow: 0 1px 1px rgba(0, 0, 0, 0.11), 0 2px 2px rgba(0, 0, 0, 0.11),
        0 4px 4px rgba(0, 0, 0, 0.11), 0 6px 8px rgba(0, 0, 0, 0.11),
        0 8px 16px rgba(0, 0, 0, 0.11);
    }
    .work-thumbnail-shadow-box {
      width: 300px;
      height: 200px;
      background: #f9ea9a;
      position: absolute;
      top: -25px;
      left: 25px;
      z-index: -1;
    }
  }

  @media screen and (max-width: 1400px) {
    .work-container {
      margin-bottom: 2rem;
    }
  }
}

@media screen and (max-width: 768px) {
  .work-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: rgb(249, 249, 249);
    border-radius: 30px;
    padding: 2rem;
    box-shadow: 0px 5px 10px 0px rgba(4, 4, 4, 0.146);
    .work-description {
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 0.3rem !important;
      .read-more {
        margin-bottom: 1rem;
        a {
          color: black;
          &:hover {
            color: rgb(83, 83, 51);
          }
        }
      }
      .description {
        font-family: "Merriweather", serif;
        margin-bottom: 1rem;
        word-spacing: 3px;
        letter-spacing: 2px;
      }
    }
  }
  .work-thumbnail {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .skills-used {
      display: flex;
      flex-wrap: wrap;
    }
  }

  .skills-used {
    display: flex;
    justify-content: center;
    align-items: center;
    .fab {
      margin: 1rem;
      font-size: 18px !important;
    }
  }
  img {
    width: 200px !important;
  }
  button {
    border: none;
    width: 100% !important;
    height: 30px;
    border-radius: 10px;
    cursor: pointer;
    background: #f9ea9a;
    transition: all 0.3s ease;
    &:hover {
      background: #ddd086;
      transition: all 0.3s ease;
    }
  }
}
