* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.workinfo-container {
  margin-top: -3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  &__stockpicker {
    padding: 3rem;
    .title {
      font-size: 1.5rem;
      .fas {
        font-size: 4rem;
        color: black;
        &:hover {
          color: #ffc107;
        }
      }
    }
    &__description {
      font-size: 1.2rem;
      margin-top: 1rem;
      color: gray;
      font-family: "Roboto", sans-serif;
      letter-spacing: 0.1rem;
    }
    &__tech {
      display: flex;
      align-items: center;
      margin-top: 1rem;
      gap: 1rem;
      h3 {
        font-size: 1.2rem;
        font-family: "Roboto", sans-serif;
        letter-spacing: 0.1rem;
      }
      .skill-container {
        display: flex;
        gap: 1rem;
      }
    }
    &__article {
      margin-top: 1rem;
      font-size: 1.2rem;
      font-family: "Roboto", sans-serif;
      letter-spacing: 0.1rem;
      padding: 3rem;

      .row-code-example {

        margin: 1rem;
        code {
          padding: 1rem;
        }
      }
      .row-introduction,
      .row-more-info,
      .row-second-example {
        font-size: 16px;
        &__images {
          display: flex;
          gap: 2rem;
          margin: 2rem;

          img {
            width: 500px;
            height: 100%;
            object-fit: cover;
            border-radius: 15px;
            box-shadow: 0 1px 1px rgba(0, 0, 0, 0.12),
              0 2px 2px rgba(0, 0, 0, 0.12), 0 4px 4px rgba(0, 0, 0, 0.12),
              0 8px 8px rgba(0, 0, 0, 0.12), 0 16px 16px rgba(0, 0, 0, 0.12);
          }
        }
        &__text {
          margin-top: 2rem;
          h1 {
            padding: 1rem;
            font-size: 2.5rem;
            color: #8393eb;
          }
          h3 {
            padding: 1rem;
          }
          &__container {
            display: flex;
          }
        }
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        &__banner {
          img {
            width: 1000px;
            height: 100%;
            border-radius: 10px;
            margin: 1rem auto;
            box-shadow: 0 1px 1px rgba(0, 0, 0, 0.12),
              0 2px 2px rgba(0, 0, 0, 0.12), 0 4px 4px rgba(0, 0, 0, 0.12),
              0 8px 8px rgba(0, 0, 0, 0.12), 0 16px 16px rgba(0, 0, 0, 0.12);
          }
        }
        p {
          padding: 1rem;
          font-weight: 400;
          color: gray;
        }
      }
    }
  }
}
